import React, { Component } from "react";
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import ParameterEditor from "./parameter-editor.js";
import ConfigForm from "./config-form";
import Diagram from './diagram';

const styles = theme => ({
  container: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'left',
  },
  controls: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'left',
    padding: '50px'
  },
  module: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    padding: '50px'
  }
});


class ModuleViewer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isParameterEditorOpen: false,
      isConfigEditorOpen: false,
      selectedUnitID: Object.keys(props.module.units)[0],
    };
  };

  setSelectedUnitID = (unitID) => {
    this.setState({
      selectedUnitID: unitID
    })
  }

  toggleParameterEditor = () => {
    this.setState({
      isParameterEditorOpen: !this.state.isParameterEditorOpen
    });
  }

  toggleConfigEditor = () => {
    this.setState({
      isConfigEditorOpen: !this.state.isConfigEditorOpen
    });
  }

  editUnitParameters = (unitID) => {
    this.setSelectedUnitID(unitID);
    this.toggleParameterEditor()
  };

  render() {
    const { classes } = this.props;
    const { module } = this.props;
    const { isParameterEditorOpen, isConfigEditorOpen, selectedUnitID } = this.state;
    return (
      <div className={classes.container}>
        <div className={classes.controls}>
          <Button className={classes.button}
            aria-owns={true ? 'simple-menu' : null}
            aria-haspopup="true"
            onClick={this.toggleConfigEditor}
            variant={"contained"}
            fullWidth={false}
          >
            config
          </Button>
        </div>
        <div className={classes.module}>
          <Diagram
            module={module}
            editUnitParameters={this.editUnitParameters}
          />
        </div>
        <ConfigForm
          module={module}
          isDrawerOpen={isConfigEditorOpen}
          toggleDrawer={this.toggleConfigEditor}
        />
        <ParameterEditor
          moduleID={module.ID}
          unitID={selectedUnitID}
          isDrawerOpen={isParameterEditorOpen}
          toggleDrawer={this.toggleParameterEditor}
        >
        </ParameterEditor>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  module: state.modules[ownProps.match.params.moduleID],
});

ModuleViewer.propTypes = {
  match: PropTypes.object.isRequired,
};

export default compose(
  connect(mapStateToProps, null),
  withStyles(styles, {name: 'ModuleViewer'})
)(ModuleViewer);
