import * as MODULES from '../module-definitions/modules';
var Immutable = require('seamless-immutable').static;

const initialModules = Immutable({});

export const modules = (state = initialModules, action) => {
  switch (action.type) {
    case "SET_MODULES": {
      let { modules } = action;
      return Immutable.merge(state, modules);
    }
    case "ADD_MODULE": {
      let { moduleType, moduleID } = action;
      let { xLoc, yLoc } = action.moduleLoc;
      let newModule = {};
      newModule[moduleID] = {...MODULES[moduleType], ID: moduleID, svgParams: {xLoc: xLoc, yLoc: yLoc}}
      return Immutable.merge(state, newModule, {deep: true})
    }
    case "REMOVE_MODULE": {
      return Immutable.without(state, action.moduleID);
    }

    case "SET_MODULE_PARAM": {
      let { moduleID, key, value } = action;
      return Immutable.setIn(state, [moduleID, key], value)
    }

    case "SET_UNIT_PARAM": {
      let { moduleID, unitID, key, value } = action;
      return Immutable.setIn(state, [moduleID, "units", unitID, key], value)
    }

    case "SET_GROUP_PARAM": {
      let { moduleID, groupID, key, value } = action;
      return Immutable.setIn(state, [moduleID, "groups", groupID, key], value)
    }

    default:
      return state;
  }
};
