import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import ProcessBuilder from './components/process-builder';
import Home from './components/home';
import ModuleViewer from './components/module-viewer';
import JupyterLab from './components/jupyter-iframe';

const MainRouter = () => (
  <div>
    <Switch>
      <Route exact path='/' component={Home}></Route>
      <Route path='/ProcessBuilder' component={ProcessBuilder}></Route>
      <Route path='/ModuleViewer/:moduleID' component={ModuleViewer}></Route>
      <Route path='/JupyterLab' component={JupyterLab}></Route>
      <Redirect from='*' to='/ProcessBuilder' />
    </Switch>
  </div>
);

export default MainRouter;
