import React, { Component } from "react";
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import { setUnitParam, requestParamChange } from '../actions/modules';
import * as parameterForms from '../module-definitions/units/parameter-forms';

const styles = theme => ({
  button: {
    margin: theme.spacing.unit,
    height: "20px",
    minHeight: "20px",
    width: "20px",
    fontSize: "12px"
  }
});


class ParameterEditor extends Component {
  
  render() {
    const { classes } = this.props;
    const { moduleID, unit } = this.props;
    let ParameterForm = parameterForms[unit.type+"_ParameterForm"];

      return (
        <Drawer 
          anchor="right" 
          open={this.props.isDrawerOpen}
        >
          <div 
            style={{"width": "400px"}}
            tabIndex={0}
          >
            <Button 
              onClick={this.props.toggleDrawer}
              variant="fab"
              color="primary"
              aria-label="close"
              className={classes.button}
            >
              x
            </Button>
            <Divider />
            <ParameterForm
              moduleID={moduleID}
              unit={unit}
              setUnitParam={this.props.setUnitParam}
              requestParamChange={this.props.requestParamChange}
            />
          </div>
        </Drawer>
      );
    }
}

const mapStateToProps = (state, ownProps) => ({
  unit: state.modules[ownProps.moduleID].units[ownProps.unitID],
});

const mapDispatchToProps = {
  setUnitParam: setUnitParam,
  requestParamChange: requestParamChange
};

ParameterEditor.propTypes = {
  classes: PropTypes.object.isRequired,
  moduleID: PropTypes.string.isRequired,
  unit: PropTypes.object.isRequired,
  isDrawerOpen: PropTypes.bool.isRequired,
  toggleDrawer: PropTypes.func.isRequired,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withStyles(styles)
)(ParameterEditor);
