import feedModuleSimulation from './feed-module-simulation';
import { setModuleParam } from '../actions/modules.js';
import { addData } from '../actions/data.js';

const simulate = function (dispatch, getState) {
  let d = new Date();
  let { modules, data } = JSON.parse(JSON.stringify(getState()));
  let newModule = {}

  Object.keys(modules).forEach((moduleID) => {
    switch (modules[moduleID].type) {
      case "FEED":
        newModule = feedModuleSimulation(modules[moduleID], data[moduleID]);
        dispatch(setModuleParam(moduleID, "units", newModule.units));
        dispatch(addData(moduleID, "BAL", {x: d.getTime(), y: newModule.units["BAL"].PV}));
        break;
      case "BAL":
        newModule = feedModuleSimulation(modules[moduleID], data[moduleID]);
        dispatch(setModuleParam(moduleID, "units", newModule.units));
        dispatch(addData(moduleID, "BAL", {x: d.getTime(), y: newModule.units["BAL"].PV}));
        break;
      default:
        console.log("module not defined");
    }
  });
};

export default simulate;
