//var Immutable = require('seamless-immutable').static;


// g/min
const MAX_FLOW_RATE = 1000.
// rev/s
//const MAX_RPM = 60. 
// minutes
//const DT = 2. / 60. 

const calcMC = (MC) => {
  MC.PV = MC.SP + (0.5-Math.random()) * 0.05 * MC.SP;
  MC.output = MC.PV / MAX_FLOW_RATE * 100;
}

const calcPCV = (PCV) => {
  PCV.PV = PCV.SP + (0.5-Math.random()) * 0.05 * PCV.SP;
  PCV.output = PCV.PV / 100. * 100;
}

const calcFCV = (FCV) => {
  FCV.PV = FCV.SP + (0.5-Math.random()) * 0.05 * FCV.SP;
  FCV.output = FCV.PV / 100. * 100;
}

const calcSTR = (STR) => {
  STR.PV = STR.SP + (0.5-Math.random()) * 0.05 * STR.SP;
  STR.output = STR.PV / 100. * 100;
}

const calcBAL = function(BAL, MC) {
  BAL.PV = BAL.PV - MC.PV * (3./60.)
}

const calcPMP = function(PMP, MC) {
  PMP.SP = MC.SP;
  PMP.PV = MC.PV + (0.5-Math.random()) * 0.1 * MC.PV;
  PMP.output = PMP.PV / MAX_FLOW_RATE * 100;
}

const calcFM = function(FM, MC) {
  FM.PV = MC.PV + (0.5-Math.random()) * 0.1 * MC.PV;
}

const calcPT = function(PT) {
  PT.PV = PT.SP + (0.5-Math.random()) * 0.1 * PT.SP;
}

const calcTT = function(TT) {
  TT.PV = TT.SP + (0.5-Math.random()) * 0.1 * TT.SP;
}

const feedModuleSimulation = (module, data) => {

  // calculate balance rate
  //let delta = deltaBAL(module.units.BAL);
  //console.log(delta);
  calcMC(module.units.MC);
  calcPCV(module.units.PCV);
  calcFCV(module.units.FCV);
  calcSTR(module.units.STR);
  calcBAL(module.units.BAL, module.units.MC);
  calcPMP(module.units.PMP, module.units.MC);
  calcFM(module.units.FM, module.units.MC);
  calcPT(module.units.PT);
  calcTT(module.units.TT);
  return module;
}

export default feedModuleSimulation;
