import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import { withStyles } from '@material-ui/core/styles';
import PaletteIcon from './palette-icon';
import * as paletteIcons from '../module-definitions/palette-icons';

const styles = theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    flexWrap: 'nowrap',
    justifyContent: 'left',
    overflow: 'auto',
    height: 'calc(100vh - 200px)',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
});

class ModuleSelector extends Component {

  render() {

    const { classes } = this.props;
    
    let equipmentIcons = [];
    let sensorIcons = [];
    let hardwareIcons = [];
    let moduleIcons = [];
    Object.keys(paletteIcons).forEach((key, indx) => {
      let IconSVG = paletteIcons[key];
      let [moduleType, category] = key.split("_");
      if (category === "Equipment") {
        equipmentIcons.push(
              <ListItem key={moduleType+indx}>
                <ListItemIcon>
                  <PaletteIcon
                    moduleType={moduleType}
                  >
                    <IconSVG />
                  </PaletteIcon>
                </ListItemIcon>
              </ListItem>
        );
      }
      else if (category === "Sensor") {
        sensorIcons.push(
            <ListItem key={moduleType+indx}>
              <ListItemIcon>
                <PaletteIcon
                  moduleType={moduleType}
                >
                  <IconSVG />
                </PaletteIcon>
              </ListItemIcon>
            </ListItem>
        );
      }
      else if (category === "Hardware") {
        hardwareIcons.push(
            <ListItem key={moduleType+indx}>
              <ListItemIcon>
                <PaletteIcon
                  moduleType={moduleType}
                >
                  <IconSVG />
                </PaletteIcon>
              </ListItemIcon>
            </ListItem>
        );
      }
      else if (category === "Module") {
        moduleIcons.push(
            <ListItem key={moduleType+indx}>
              <ListItemIcon>
                <PaletteIcon
                  moduleType={moduleType}
                >
                  <IconSVG />
                </PaletteIcon>
              </ListItemIcon>
            </ListItem>
        );
      }
    });

    return (
      <div className={classes.container}>
        <ExpansionPanel>
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
            <Typography className={classes.heading}>Equipment</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <List>
              {equipmentIcons}
            </List>
          </ExpansionPanelDetails>
        </ExpansionPanel>
        <ExpansionPanel>
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
            <Typography className={classes.heading}>Sensors</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <List>
              {sensorIcons}
            </List>
          </ExpansionPanelDetails>
        </ExpansionPanel>
        <ExpansionPanel>
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
            <Typography className={classes.heading}>Hardware</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <List>
              {hardwareIcons}
            </List>
          </ExpansionPanelDetails>
        </ExpansionPanel>
        <ExpansionPanel>
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
            <Typography className={classes.heading}>Modules</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <List>
              {moduleIcons}
            </List>
          </ExpansionPanelDetails>
        </ExpansionPanel>
      </div>
    );
  }
};

ModuleSelector.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles, {name: 'ModuleSelector'})(ModuleSelector);
