import React, { Component } from "react";
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { Link } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Save from '@material-ui/icons/Save';
import SaveIcon from '@material-ui/icons/Save';
import FolderOpen from '@material-ui/icons/FolderOpen';
import DoneAllOutlined from '@material-ui/icons/DoneAllOutlined';
import { setProcessTitle } from '../actions/user-data';
import { setModules, startDemo } from '../actions/modules';

const styles = theme => ({
  root: {
    //flexGrow: 1,
    height: '60px',
    width: '100%',
    borderBottom: '3px solid gray',
  },
  title: {
    marginLeft: '100px'
  },
  button: {
    fontSize: '12px',
    marginLeft: '20px',
  },
  icon: {
    fontSize: '15px',
    marginRight: '10px'
  },
});


class ProcessToolbar extends Component {

  setTitle = (event) => {
    this.props.setProcessTitle(event.target.value);
  }

  saveProcess = (name) => {
    localStorage.setItem(
        name, 
        JSON.stringify(this.props.modulesState)
    );
  }

  loadProcess = (name) => {
    let modules = JSON.parse(localStorage.getItem(name));
    this.props.setModules(modules);
  }

  render() {
    const { classes } = this.props;
    const { processTitle, showModuleDetails, toggleShowModuleDetails } = this.props;
    return (
      <div className={classes.root}>
        <Toolbar style={{height: '50px'}}>
          <Button
            size="small"
            className={classes.button}
            color="primary"
          >
            <Link to='/ProcessBuilder'>Process Builder</Link>
          </Button>
          <Button
            size="small"
            className={classes.button}
            color="primary"
          >
            <Link to='/JupyterLab'>Jupyter Lab</Link>
          </Button>
          <Button
            size="small"
            className={classes.button}
            color="primary"
          >
            Node-Red
          </Button>
          <TextField
            label="Process Title"
            value={processTitle}
            className={classes.title}
            onChange={this.setTitle}
          />
          <Button 
            size="small" 
            color="primary"
            onClick={()=>{this.saveProcess(processTitle)}}
            variant="outlined"
          >
            <SaveIcon className={classes.icon} />
            Save
          </Button>
          <Button 
            size="small" 
            color="primary"
            onClick={()=>{this.loadProcess(processTitle)}}
            variant="outlined"
          >
            <FolderOpen className={classes.icon} />
            Load
          </Button>
          <Button 
            size="small" 
            color="primary"
            onClick={()=>{this.props.startDemo()}}
            variant="outlined"
          >
            <DoneAllOutlined className={classes.icon} />
            Deploy
          </Button>
        </Toolbar>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  processTitle: state.userData.processTitle,
  modulesState: state.modules
});

const mapDispatchToProps = {
  setProcessTitle,
  setModules,
  startDemo,
};

ProcessToolbar.propTypes = {
  classes: PropTypes.object.isRequired,
  processTitle: PropTypes.string.isRequired,
  modulesState: PropTypes.object.isRequired,
  setProcessTitle: PropTypes.func.isRequired,
  setModules: PropTypes.func.isRequired,
};

export default compose(
  connect( mapStateToProps, mapDispatchToProps),
  withStyles(styles, {name: 'ProcessToolbar'}),
)(ProcessToolbar);
