var Immutable = require('seamless-immutable').static;

const initialData = Immutable({});

export const data = (state = initialData, action) => {
  let { newData, moduleID, unitID } = action;
  switch (action.type) {
    case "ADD_DATA":
      if (state.hasOwnProperty(moduleID)) {
        if (state[moduleID].hasOwnProperty(unitID)) {
          return Immutable.setIn(state, [moduleID, unitID], [...state[moduleID][unitID], newData]);
        }
        else {
          return Immutable.setIn(state, [moduleID], {unitID: [newData]});
        }
      }
      else {
        let newUnit = {};
        newUnit[unitID] = [newData];
        let newModule = {};
        newModule[moduleID] = newUnit;
        return Immutable.merge(state, newModule);
      }
    default:
      return state;
  }
};
