import React, { Component } from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import * as diagrams from '../module-definitions/diagrams';

const styles = theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  button: {
    width: "100px"
  }
});

class Diagram extends Component {

  render() {
    const { classes } = this.props;
    const { module } = this.props;
    let DiagramSVG = diagrams[module.type+"_Diagram"];
    return (
      <div className={classes.container}>
        <DiagramSVG 
          module={module}
          editUnitParameters={this.props.editUnitParameters}
          height='100mm'
          width='100mm'
        /> 
      </div>
    );
  }
}


Diagram.propTypes = {
  module: PropTypes.object.isRequired,
  editUnitParameters: PropTypes.func,
};

export default withStyles(styles, {name: 'Diagram'} )(Diagram);
