import { setModuleParam } from '../actions/modules.js';

const initializeFeed = (module) => {
  if (module.units.MC.SP == 0) {
    let SP = (Math.random()+1)*20 + (0.5-Math.random())*5;
    module.units.MC.SP = SP;
  }
  return module;
}

const demo = function (dispatch, getState) {
  let { modules, data } = JSON.parse(JSON.stringify(getState()));
  let newModule = {}

  Object.keys(modules).forEach((moduleID) => {
    switch (modules[moduleID].type) {
      case "FEED":
        newModule = initializeFeed(modules[moduleID])
        break;
      default:
        console.log("module not defined");
    }
    dispatch(setModuleParam(moduleID, "units", newModule.units));
  });
};

export default demo;
