import React, { Component } from "react";
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import HTML5Backend from 'react-dnd-html5-backend';
import { DragDropContext } from 'react-dnd';
import { withStyles } from '@material-ui/core/styles';
import ProcessToolbar from './process-toolbar';
import ProcessMap from './process-map';
import ModuleSelector from './module-selector';
import ModuleDelete from './module-delete';
import MapModuleDetails from './map-module-details';

const styles = theme => ({
  container: {
  },
  body: {
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'nowrap',
    height: 'calc(100vh - 128px)',
    width: '100%',
  },
  sidePanel: {
    width: '140px',
    height: 'calc(100vh - 128px)',
    display: 'flex',
    flexDirection: 'column',
    borderRight: '3px solid gray',
  },
  processMap: {
    display: 'flex',
    flexDirection: 'row',
    height: 'calc(100vh - 128px)',
    width: 'calc(100vw - 140px)',
  },
});

class ProcessBuilder extends Component {
  state = {
    showModuleDetails: true,
    selectedModule: null,
  };

  openModuleDetails = () => {
    this.setState({
      showModuleDetails: true,
    });
  }

  closeModuleDetails = () => {
    this.setState({
      showModuleDetails: false,
    });
  }

  setSelectedModule= (module) => {
    this.setState({
      selectedModule: module
    });
  }

  render() {
    const { classes } = this.props;
    const { showModuleDetails, selectedModule } = this.state;
    const openModuleDetails = this.openModuleDetails;
    const setSelectedModule = this.setSelectedModule;
    const closeModuleDetails = this.closeModuleDetails;

    return (
      <div className={classes.container}>
        <ProcessToolbar />
        <div className={classes.body}>
          <div className={classes.sidePanel}>
            <ModuleSelector />
            <ModuleDelete style={{bottom: 0}} />
          </div>
          <div className={classes.processMap}>
            <ProcessMap 
              setSelectedModule={setSelectedModule}
              openModuleDetails={openModuleDetails}
            />
            <MapModuleDetails
              isOpen={showModuleDetails}
              module={selectedModule}
              closeWindow={closeModuleDetails}
            />
          </div>
        </div>
      </div>
    );
  }
};

const mapStateToProps = state => ({
});

const mapDispatchToProps = {
};

ProcessBuilder.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  connect( mapStateToProps, mapDispatchToProps),
  withStyles(styles, {name: 'ProcessBuilder'} ),
  DragDropContext(HTML5Backend)
)(ProcessBuilder);
