import React, { Component } from "react";
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { DropTarget } from 'react-dnd';
import ItemTypes from '../ItemTypes';
import { withStyles } from '@material-ui/core/styles';
import { addModule } from '../actions/modules';
import MapIcon from './map-icon';
import * as mapIcons from '../module-definitions/map-icons';

const styles = theme => ({
  container: {
    display: 'flex',
    flexDirection: 'row',
    height: 'calc(100vh - 114px)',
    width: '100%',
  },
});

const moduleTarget = {
  drop(props, monitor, component) {
    const item = monitor.getItem();
    if (item.itemType === "icon") {
      const delta = monitor.getClientOffset();
      item.addModule(
        Math.floor(delta.x),
        Math.floor(delta.y)
      );
    }
    else if (item.itemType==="module") {
      const delta = monitor.getDifferenceFromInitialOffset();
      item.setLoc(
        item.moduleID,
        Math.floor(item.xLoc + delta.x),
        Math.floor(item.yLoc + delta.y)
      );
    }
  }
}
    
function collect(connect, monitor) {
  return {
    connectDropTarget: connect.dropTarget(),
  }
};

class ProcessMap extends Component {

  handleModuleClick = (module) => {
    this.props.setSelectedModule(module);
    this.props.openModuleDetails();
  }

  render() {
    const { classes } = this.props;
    const { connectDropTarget } = this.props;
    const { modules } = this.props;
    let handleModuleClick = this.handleModuleClick;

    let display_modules = Object.keys(modules).map((key, index) => {
      let IconSVG = mapIcons[modules[key].type+"_MapIcon"];
      return (
        <div
            onClick={()=>{handleModuleClick(modules[key])}}
            key={index}
        >
          <MapIcon
            module={modules[key]}
            key={index} 
            xLoc={modules[key].svgParams.xLoc}
            yLoc={modules[key].svgParams.yLoc}
          >
            <IconSVG 
              moduleID={module.ID}
            />
          </MapIcon>
        </div>
      );
    });
    return connectDropTarget(
      <div
        className={classes.container}
      >
          {display_modules}
      </div>
    );
  }
};

const mapStateToProps = state => ({
  modules: state.modules,
});

const mapDispatchToProps = {
  addModule,
};

ProcessMap.propTypes = {
  setSelectedModule: PropTypes.func.isRequired,
  openModuleDetails: PropTypes.func.isRequired,
  modules: PropTypes.object.isRequired,
  addModule: PropTypes.func.isRequired,
  connectDropTarget: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
};

export default compose(
  connect( mapStateToProps, mapDispatchToProps),
  withStyles(styles, {name: 'ProcessMap'} ),
  DropTarget([ItemTypes.MODULE_ICON, ItemTypes.MODULE], moduleTarget, collect)
)(ProcessMap);
