import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Switch from '@material-ui/core/Switch';
import SimpleListMenu from '../../components/simple-menu';

const styles = {
};

const modeOptions = [
  {option: "PMP / BAL", measure: "BAL", control: "PMP"},
  {option: "PMP / FM", measure: "FM", control: "PMP"},
  {option: "FCV / BAL", measure: "BAL", conrtrol: "FCV"},
  {option: "FCV / FM", measure: "FM", control: "FCV"},
  {option: "PMP", measure: "none", control: "PMP"}
];

class ConfigurationForm extends React.Component {

  toggleRecycleLineVisible = () => {
    let visible = this.props.module.groups.RECYCLE_LINE.visible;
    this.props.setGroupParam(this.props.module.ID, "RECYCLE_LINE", "visible", !visible)
  }

  toggleSolventLineVisible = () => {
    let visible = this.props.module.groups.SOLVENT_LINE.visible;
    this.props.setGroupParam(this.props.module.ID, "SOLVENT_LINE", "visible", !visible)
  }

  setMode = (index) => {
    this.props.setModuleParam(this.props.module.ID, "mode", modeOptions[index]);
  }
  
  handleEquipmentCheck = unitID => event => {
    this.props.setUnitParam(
      this.props.module.ID,
      unitID,
      "active",
      event.target.checked
    )
  };

  handlePortSelection = unitID => event => {
    this.props.setUnitParam(
      this.props.module.ID,
      unitID,
      "connection",
      this.props.module.units[unitID].connectionOptions[event]
    )
  }

  handleModelSelection = unitID => event => {
    this.props.setUnitParam(
      this.props.module.ID,
      unitID,
      "model",
      this.props.module.units[unitID].modelOptions[event]
    )
  }

  render() {
    const { module } = this.props;
    let units = module.units;

    const equipmentList = Object.keys(units).map((unitID) => {
      return (
        <FormGroup
          key={unitID} 
        >
          <FormControlLabel
            control={
              <Checkbox
                checked={units[unitID].active}
                onChange={this.handleEquipmentCheck(unitID)}
                value={unitID}
                color="primary"
              />
            }
            label={unitID}
          />
          {units[unitID].active ?
            <FormGroup row >
              <SimpleListMenu
                    options={units[unitID].connectionOptions}
                    label={""}
                    disabled={[]}
                    onSelect={this.handlePortSelection(unitID)}
              />
              <SimpleListMenu
                    options={units[unitID].modelOptions}
                    label={""}
                    disabled={[]}
                    onSelect={this.handleModelSelection(unitID)}
              />
            </FormGroup>
            : ""
          }
      </FormGroup>
      )
    });

    return (
      <form noValidate autoComplete="off">
        <FormControlLabel
          control={
            <Switch
              checked={module.groups.RECYCLE_LINE.visible}
              onChange={this.toggleRecycleLineVisible}
              value="recycle_line"
              color="primary"
            />
          }
          label="Recycle Line"
        />
        <br/>
        <FormControlLabel
          control={
            <Switch
              checked={module.groups.SOLVENT_LINE.visible}
              onChange={this.toggleSolventLineVisible}
              value="solvent_line"
              color="primary"
            />
          }
          label="Solvent Line"
        />
        <br />
        <SimpleListMenu
          options={modeOptions.map((x)=>{return x.option})}
          label={"mode"}
          disabled={[]}
          onSelect={this.setMode}
        />
        <br />
        <FormGroup>
          {equipmentList}
        </FormGroup>
        <br />
      </form>
    );
  }
}

ConfigurationForm.propTypes = {
  module: PropTypes.object.isRequired,
  setModuleParam: PropTypes.func.isRequired,
  setUnitParam: PropTypes.func.isRequired,
  setGroupParam: PropTypes.func.isRequired, 
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ConfigurationForm);

