import React, { Component } from "react";
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';
import { ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip } from 'recharts';

const styles = theme => ({
  container: {
  }
});

class SimpleScatterChart extends Component {
	render () {
    const { classes, moduleId, data } = this.props;

    let new_data = []
    if (data.hasOwnProperty(moduleId)) {
      let units = Object.keys(data[moduleId]);
      let unit = units[0];
      new_data = data[moduleId][unit].map((d)=>{
        return {
          x: Math.round((d['x'] - data[moduleId][unit][0]['x']) / 1000, 0) ,
          y: d['y']
        }
      });
    }
    let yMin = Math.min(...new_data.map((d) => { return Math.round(d.y) }));
    let yMax = Math.max(...new_data.map((d) => { return Math.round(d.y) }));
    let xMax = Math.max(...new_data.map((d) => { return Math.round(d.x) }));

    let show = (typeof(yMin) === "number" && Number.isFinite(yMin) 
      && typeof(yMax) === "number" && Number.isFinite(yMax))

  	return (
      show ? 
      <div className={classes.container}>
        <ScatterChart width={500} height={400} margin={{top: 20, right: 20, bottom: 20, left: 20}}>
          <CartesianGrid />
          <XAxis dataKey={'x'} type="number" name='time' unit='seconds' domain={[0, xMax]}/>
          <YAxis dataKey={'y'} type="number" name='weight' unit='g' domain={[0, yMax]}/>
          <Scatter name='A school' data={new_data} fill='#8884d8'/>
        </ScatterChart>
      </div>
      : ""
    );
  }
};

const mapStateToProps = state => ({
  data: state.data
});

SimpleScatterChart.propTypes = {
  moduleId: PropTypes.string,
};

export default compose(
  withStyles(styles, {name: 'SimpleScatterChart'} ),
  connect( mapStateToProps, null)
)(SimpleScatterChart);
