import React, { Component } from "react";
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';
import { DropTarget } from 'react-dnd';
import ItemTypes from '../ItemTypes';
import { removeModule } from '../actions/modules';
import Delete from '@material-ui/icons/Delete';

const styles = theme => ({
  container: {
    height: '50px',
    padding: '20px',
    textAlign: 'center',
    borderTop: '5px solid gray',
  },
  icon: {
    fontSize: 50,
  }
});

const moduleTarget = {
  drop(props, monitor, component) {
    console.log('test');
    const item = monitor.getItem();
    props.removeModule(item.moduleID);
  }
}
    
function collect(connect, monitor) {
  return {
    connectDropTarget: connect.dropTarget(),
  }
};

class ModuleDelete extends Component {

  render() {
    const { classes } = this.props;
    const { connectDropTarget } = this.props;

    return connectDropTarget(
      <div className={classes.container}>
        <Delete className={classes.icon}/>
      </div>
    );
  }
};

const mapStateToProps = state => ({
  modules: state.modules
});

const mapDispatchToProps = {
  removeModule
};

ModuleDelete.propTypes = {
  connectDropTarget: PropTypes.func.isRequired
};

export default compose(
  connect( mapStateToProps, mapDispatchToProps),
  DropTarget(ItemTypes.MODULE, moduleTarget, collect),
  withStyles(styles, {name: 'ModuleDelete'} ),
)(ModuleDelete);
