import React, { Component } from "react";
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import Slide from '@material-ui/core/Slide';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import SimpleScatterChart from './chart';
import * as diagrams from '../module-definitions/diagrams';

const styles = theme => ({
  slide: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
    width: '900px',
    justifyContent: 'top',
    alignItems: 'center',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  button: {
    margin: theme.spacing.unit,
    height: "20px",
    minHeight: "20px",
    width: "20px",
    fontSize: "12px",
  }
});


class MapModuleDetails extends Component {

  render() {
    const { classes, isOpen, module, modules, closeWindow } = this.props;

    const display = () => {
      if (module !== null) {
        let DiagramSVG = diagrams[this.props.module.type+"_Diagram"];
        return (
          <Paper
            className={classes.slide}
            elevation={10}
          >
          <div style={{width: '100%', float: 'left'}}>
            <Button 
              onClick={closeWindow}
              variant="fab"
              color="primary"
              aria-label="close"
              className={classes.button}
            >
              x
            </Button>
          </div>
          <h3>{module.ID}</h3>
          <ExpansionPanel style={{width: '100%'}}>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <Typography className={classes.heading}>Module</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Link to={'/ModuleViewer/'+module.ID}>
                <Button 
                  variant={"contained"}
                >
                  edit
                </Button>
              </Link>
              <DiagramSVG 
                module={modules[module.ID]}
                height='400px'
                width='400px'
              /> 
            </ExpansionPanelDetails>
          </ExpansionPanel>
          <ExpansionPanel style={{width: '100%'}}>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <Typography className={classes.heading}>Chart</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <SimpleScatterChart 
                moduleId={module.ID}
              />
            </ExpansionPanelDetails>
          </ExpansionPanel>
          </Paper>
        )
      }
      else {
        return (
          <p></p>
        )
      }
    }

    return (
      <Slide 
        in={isOpen}
        direction="left"
      >
          {display()}
      </Slide>
    );
  }
}

const mapStateToProps = state => ({
  // Had to pass modules from state to get the diagram
  // to update. TODO look into why passing the module 
  // directly doesn't work for updating.
  modules: state.modules,
});

MapModuleDetails.propTypes = {
  module: PropTypes.object,
  isOpen: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired,
};

export default compose(
  connect( mapStateToProps, null),
  withStyles(styles, {name: 'MapModuleDetails'} ),
)(MapModuleDetails);
