import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SimpleListMenu from '../../../components/simple-menu';

const styles = theme => ({
  container: {
    display: 'block',
    flexWrap: 'wrap',
    padding: "20px"
  },
});

class ParameterForm extends React.Component {

  handleSPChange = (index) => {
    let SP = this.props.unit.SPOptions[index]
    this.props.setUnitParam(this.props.moduleID, this.props.unit.ID, "SP", SP)
    this.props.requestParamChange(this.props.moduleID, this.props.unit.ID, "SP", SP)
  }

  render() { 
    const { classes } = this.props;
    const { unit } = this.props;

    return (
      <div className={classes.container}>
        <SimpleListMenu
          options={unit.SPOptions}
          label={"SP"}
          disabled={[]}
          onSelect={this.handleSPChange}
        />
      </div>
    );
  }
}

ParameterForm.propTypes = {
  classes: PropTypes.object.isRequired,
  moduleID: PropTypes.string.isRequired,
  unit: PropTypes.object.isRequired,
};

export default withStyles(styles, {name: 'ParameterForm'} )(ParameterForm);
