import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';
import { DragSource } from 'react-dnd';
import ItemTypes from '../ItemTypes';
import { addModule } from '../actions/modules';

const styles = theme => ({
  container: {
    width: "70px",
  },
});

const itemSource = {
  beginDrag(props, monitor, component) {
    const addModule = (xLoc, yLoc) => {
      let moduleType = props.moduleType;
      let d = new Date();
      let moduleID = moduleType+"_"+String((((d.getTime() / 1000000) %1)*1000).toFixed(0));
      props.addModule(moduleType, moduleID, {xLoc: xLoc, yLoc: yLoc});
    };
    return {
      itemType: "icon",
      addModule: addModule,
    };
  }
};

function collect(connect, monitor) {
  return {
    connectDragSource: connect.dragSource(),
    xLoc: monitor.getInitialClientOffset()
  }
}

class PaletteIcon extends Component {

  render() {
    const { classes } = this.props;
    const { connectDragSource } = this.props;
    //console.log(this.props);
    return connectDragSource(
      <div className={classes.container}>
        {this.props.children}
      </div>
    );
  }
};

const mapDispatchToProps = {
  addModule,
};

PaletteIcon.propTypes = {
  connectDragSource: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  addModule: PropTypes.func.isRequired,
};

export default compose(
  connect( null, mapDispatchToProps),
  withStyles(styles, {name: 'PaletteIcon'}),
  DragSource(ItemTypes.MODULE_ICON, itemSource, collect)
)(PaletteIcon);
