import React, { Component } from "react";
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';
import { DragSource } from 'react-dnd';
import ItemTypes from '../ItemTypes';
import { setModuleParam } from '../actions/modules';
import SimpleScatterChart from './chart';

const styles = theme => ({
  container: {
    display: "flex",
    flexDirection: "row",
    position: "absolute",
  },
});

const itemSource = {
  beginDrag(props, monitor, component) {
    const setLoc = (moduleID, x, y) => {
      props.setModuleParam(props.module.ID, "svgParams", {xLoc: x, yLoc: y}) 
    }
    return {
      itemType: "module",
      moduleID: props.module.ID,
      xLoc: props.xLoc,
      yLoc: props.yLoc,
      setLoc: setLoc
    };
  }
};

function collect(connect, monitor) {
  return {
    connectDragSource: connect.dragSource(),
  }
}

class MapIcon extends Component {

  render() {
    const { classes } = this.props;
    const { connectDragSource } = this.props;
    const { module, xLoc, yLoc, children } = this.props;
    let iconSVG = React.cloneElement(children, { module: this.props.module })
    return connectDragSource(
      <div className={classes.container} 
        style={{left: xLoc, top: yLoc}} 
      >
        {iconSVG}
      </div>
    );
  }
};

const mapDispatchToProps = {
  setModuleParam
};

MapIcon.propTypes = {
  connectDragSource: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  module: PropTypes.object.isRequired,
};

export default compose(
  connect( null, mapDispatchToProps),
  withStyles(styles, {name: 'MapIcon'}),
  DragSource(ItemTypes.MODULE, itemSource, collect),
)(MapIcon);
