import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';

const styles = theme => ({
  container: {
    display: 'block',
    flexWrap: 'wrap',
    padding: "20px"
  },
  button: {
    margin: theme.spacing.unit,
    height: "20px"
  },
  input: {
    display: 'none',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  }

});

class ParameterForm extends React.Component {

  state = {
    SP: this.props.unit.SP
  }

  handleSPChange = (event) => {
    // Continually update the TextField value.
    this.setState({
      SP: Number(event.target.value)
    });
  };

  handleSPKeyPress = (event) => {
    // If the TextField receives "Enter" update the store SP
    // and send SP change request.
    if(event.key === 'Enter'){
      this.props.setUnitParam(this.props.moduleID, this.props.unit.ID, "SP", this.state.SP)
      this.props.requestParamChange(this.props.moduleID, this.props.unit.ID, "SP", this.state.SP)
    }
  }

  render() { 
    const { classes } = this.props;
    const { unit } = this.props;

    return (
      <div>
        <TextField
          id="SP"
          label="SP"
          value={this.state.SP.toFixed(1)}
          type={"number"}
          className={classes.textField}
          margin="normal"
          onChange={this.handleSPChange}
          onKeyPress={this.handleSPKeyPress}
        />
        <TextField
          disabled={true}
          id={"PV"}
          label={"PV"}
          type={"number"}
          value={unit.PV.toFixed(1)}
          className={classes.textField}
          margin="normal"
        />
        <TextField
          disabled={true}
          id={"output"}
          label={"output"}
          value={unit.output.toFixed(1)}
          className={classes.textField}
          margin="normal"
        />
      </div>
    );
  }
}

ParameterForm.propTypes = {
  classes: PropTypes.object.isRequired,
  moduleID: PropTypes.string.isRequired,
  unit: PropTypes.object.isRequired,
};

export default withStyles(styles, {name: 'ParameterForm'} )(ParameterForm);
