import simulate from './process-simulation';
import demo from './demo';

var simID = null

export const setModules = (modules) => ({
  type: "SET_MODULES",
  modules,
});

export const addModule = (moduleType, moduleID, moduleLoc) => ({
  type: "ADD_MODULE",
  moduleType,
  moduleID,
  moduleLoc
});

export const removeModule = (moduleID) => ({
  type: "REMOVE_MODULE",
  moduleID
});

export const setModuleParam = (moduleID, key, value) => ({
  type: "SET_MODULE_PARAM",
  moduleID,
  key,
  value,
});

export const setUnitParam = (moduleID, unitID, key, value) => ({
  type: "SET_UNIT_PARAM",
  moduleID,
  unitID,
  key,
  value
});

export const setGroupParam = (moduleID, groupID, key, value) => ({
  type: "SET_GROUP_PARAM",
  moduleID,
  groupID,
  key,
  value
});

export function requestParamChange(moduleID, unitID, param, value) {
  // Handles all user requests to change a parameter, by dispatching one of two methods:
  // 1. Make a remote call to the equipment (MQTT, OPC-UA, ...etc)
  // 2. Call a demo/simulation mode function on interval passing the current state.
  return function(dispatch, getState) {
    dispatch(setUnitParam(moduleID, unitID, param, value))
    // If user has selected demo mode then do a local simulation
    if ( simID !== null) {clearInterval(simID)}
    simID = setInterval(simulate, 3000, dispatch, getState);
  };
};

export function startDemo() {
  // Set reasonable PV, SP values for the various equipment, and start simulation.
  return function(dispatch, getState) {
    // If user has selected demo mode then do a local simulation
    demo(dispatch, getState);
    if ( simID !== null) {clearInterval(simID)}
    simID = setInterval(simulate, 3000, dispatch, getState);
  };
};
