import React from 'react';
import PropTypes from 'prop-types';
import { connect } from "react-redux";
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import { setModuleParam, setUnitParam, setGroupParam } from '../actions/modules';
import * as configurationForms from '../module-definitions/configuation-forms';

const styles = theme => ({
  container: {
    width: "400px",
  },
  button: {
    margin: theme.spacing.unit,
    height: "20px",
    minHeight: "20px",
    width: "20px",
    fontSize: "12px"
  }
});


class ConfigForm extends React.Component {

  state = {
    anchorEl: null,
  };

  render() {
    const { module, setModuleParam, setUnitParam, setGroupParam } = this.props;
    const { classes } = this.props;
    let ConfigurationForm = configurationForms[module.type+"_ConfigurationForm"];

    return (
      <Drawer 
        anchor="right" 
        open={this.props.isDrawerOpen}
      >
        <div
          className={classes.container}
        >
          <Button 
            onClick={this.props.toggleDrawer}
            variant="fab"
            color="primary"
            aria-label="close"
            className={classes.button}
          >
            x
          </Button>
          <h3>Configuration Form</h3>
          <Divider />
          <ConfigurationForm 
            module={module}
            setModuleParam={setModuleParam}
            setUnitParam={setUnitParam}
            setGroupParam={setGroupParam}
          />
        </div>
      </Drawer>
    );
  }
}

const mapDispatchToProps = {
  setModuleParam: setModuleParam,
  setUnitParam: setUnitParam,
  setGroupParam: setGroupParam,
};

ConfigForm.propTypes = {
  classes: PropTypes.object.isRequired,
  module: PropTypes.object.isRequired,
  setModuleParam: PropTypes.func.isRequired, 
  setUnitParam: PropTypes.func.isRequired, 
  setGroupParam: PropTypes.func.isRequired, 
};

export default compose(
  connect( null, mapDispatchToProps),
  withStyles(styles, {name: 'FeedModule'} ),
)(ConfigForm);
